.navbar {
	position:relative;
	border-radius: 0px;
	margin: 0px;
	padding: 10px;
}

.navbar .navbar-nav > li > a {
  	color: white;
  	width: 100px;
  	height: 100%;
}

.navbar .navbar-nav > li > a:hover {
  	color: #FFD602;
}

.navbar .navbar-menu-item > li > a {
  	color: white;
}

.navbar .navbar-nav > li > a:hover {
  	color: #FFD602;
}

.logo {
	margin-top: -10px;
}

.blanket {
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.1);
}

.carousel {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.hoverDiv {
	position: absolute;
	width: 100%;
	text-align: center;
}

.hoverDiv h1 {
	color: white;
	font-size: 80px;
	font-weight: bold;
}

.hoverDiv h3 {
	color: white;
	margin-bottom: 40px;
}

.carousel b {
	letter-spacing: 1px;
}

.carousel .button {
	background-color: rgba(0,0,0,0.4);
	color: #FFD602;
	border-color: #FFD602;
	border-width: 3px;
	width: 25vw;
	padding: 15px;
	font-size: 25px;
	min-width: 250px;
}

.carousel .button:hover {
	background-color: #FFD602;
	color: black;
}


.footer .logo {
	margin: 20px 0px;
}

.footer .gridRow {
	background-color: #1a1a1a; 
	text-align: center;
}

.footer .cell0 {
	padding: 20px;
	color: white;
}

.footer .cell0 ul {
	list-style-type: none;
}

.footer .cell0 li {
	margin: 10px;
	text-decoration: none;
}

.footer .cell0 a {
	color:white;
}

.footer .cell0 a:hover {
	color: lightgrey;
	text-decoration: none;
}

.footer .cell1 {
	padding: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.footer .cell1 .button {
	background-color: #FFD602;
	border-color: black;
	width: 200px;
	margin: 0 auto;
}

.footer .cell1 .button:hover {
	background-color: black;
	border-color: #FFD602;
	color: #FFD602;
}

.footer .cell2 {
	padding: 20px;
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}


@media only screen and (min-width: 768px) {
    .footer .gridRow {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
	}
}




