.home .cell0 {
	padding: 80px 0px;
	text-align: center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	font-size: 17px;
}

.home .cell0 h2 {
	font-size: 40px;
	margin-bottom: 20px;
}

.home .cell1 .imageDiv {
	display: grid;
}

.home .cell1 .image {
	width: 100%;
	border-style: solid;
	border-color: black;
	border-width: 1px;
}

.home .cell1 .text {
	background-color: white;
	text-align: center;
	padding: 30px 40px 70px 40px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.home .cell1 .text h3 {
	font-weight: bold;
}

.home .cell1 .text a {
	color: black;
}

.home .cell1 .text a:hover {
	text-decoration: none;
}

.home .cell2 {
    /* Set a specific height */
    min-height: 500px; 

    /* Create the parallax scrolling effect */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.home .cell2 .blanket {
	width:100%;
	height: 100%;
	position: absolute;
	background-color: rgba(0,0,0,0.4);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.home .cell2 h3 {
	font-size: 40px;
	font-weight: bold;
	margin-bottom: 30px;
}

.centerText {
	position: absolute;
	width: 100%;
	text-align: center;
	color: white;
	padding: 0px 20px;
}

.home .cell3 {
	z-index: 100;
	position: relative;
	text-align: center;
	padding: 60px;
	background-color: #f2f2f2;
}

.home .cell3 h2 {
	font-weight: bold;
}

@media only screen and (min-width: 768px) {
    .home .cell1 {
		display: grid;
		grid-template-columns: 1fr 1fr 1fr;
		background-color: black;
	}
}


